import { useMemo } from "react"
import { BoundingBox } from "@rp/common/api-types"
import { useHashParams } from "@rp/react/web/common/hooks"

type UsePageBoundingBoxState = {
  boundingBox: BoundingBox
}

const usePageBoundingBox = (): UsePageBoundingBoxState => {
  const [{ bb: bbStringified = "{}" }] = useHashParams()

  const boundingBox = useMemo(() => {
    const bbRaw = JSON.parse(bbStringified) as BoundingBox

    const normalizedBB = {
      top: bbRaw.top ?? 0,
      right: bbRaw.right ?? 1,
      bottom: bbRaw.bottom ?? 1,
      left: bbRaw.left ?? 0,
      page: bbRaw.page ?? 0,
    }

    return normalizedBB
  }, [bbStringified])

  return { boundingBox }
}

export { usePageBoundingBox }
export type { UsePageBoundingBoxState }
